;height:100vh;top:0;left:0;position:fixed;z-index:9999}.overlay .playAgain{border-radius:5px 5px 5px 5px;margin:0 auto;background-color:#2c2c2c;padding:4%;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.overlay .playAgain button{margin:0 2% 0 2%}.overlay .playAgain button:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.show{display:block}html,body,#root{height:100vh;width:100vw;margin:0;background:radial-gradient(circle at center, #3437e6, black)}:root{font-size:16px}@media (max-width: 1025px) and (min-width: 768px){:root{font-size:12px}}@media (max-width: 767px){:root{font-size:10px}}@media (min-width: 2560px){:root{font-size:40px}}.app{font-family:"Orbitron",sans-serif;box-sizing:border-box;text-align:center;color:white;height:100%;width:100%}div{margin:0}h1{font-family:"Black Ops One",sans-serif;font-size:7rem;text-transform:uppercase;margin:0;background:-webkit-linear-gradient(#e1ff5c, #c21212);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-text-stroke:0.01em black}@media (max-width: 767px){h1{font-size:4rem}}h2{letter-spacing:0.1em;font-size:1rem;margin:0;text-transform:uppercase}p{margin:0;font-size:0.9rem}button{height:-webkit-min-content;height:min-content;text-transform:uppercase;font-family:"Orbitron",sans-serif;font-size:0.9rem;padding:1%;color:white;background-color:#2c2c2c;border-top:.3rem solid #464646;border-bottom:.3rem solid #000;border-left:.3rem solid #131313;border-right:.3rem solid #131313;outline:none}button:hover{cursor:pointer;color:orange}

